import React from "react";
import TestPackageImg from "../assets/images/TestPackage__card-1.svg";
import TestPackageImg2 from "../assets/images/TestPackage__card-2.svg";

function TestPackageCards() {
  return (
    <>
      <div className="TestPackage__wrp">
        <div className="TestPackage__card">
          <img src={TestPackageImg} alt="TestPackage__card img" />
          <p>Driving test package with driving lessons</p>
        </div>
        <div className="TestPackage__card">
          <img src={TestPackageImg2} alt="TestPackage__card img" />
          <p>Stand alonedriving test package</p>
        </div>
      </div>
    </>
  );
}

export default TestPackageCards;
